/* Floating particles animation */
@keyframes float {
        0%,
        100% {
            transform: translateY(0) translateX(0);
        }
        25% {
            transform: translateY(-20px) translateX(10px);
        }
        50% {
            transform: translateY(-10px) translateX(-10px);
        }
        75% {
            transform: translateY(-30px) translateX(5px);
        }
    }
@keyframes glow-pulse {
        0%,
        100% {
            opacity: 0.4;
            transform: scale(1);
        }
        50% {
            opacity: 0.8;
            transform: scale(1.1);
        }
    }
@keyframes border-flow {
        0%,
        100% {
            background-position: 0% 50%;
        }
        50% {
            background-position: 100% 50%;
        }
    }
@keyframes shimmer-slide {
        0% {
            transform: translateX(-100%);
        }
        100% {
            transform: translateX(100%);
        }
    }
/* Smooth scrolling */
html {
        scroll-behavior: smooth;
    }
.glass {
        background: linear-gradient(135deg, rgba(16, 185, 129, 0.15) 0%, rgba(5, 150, 105, 0.08) 100%);
        backdrop-filter: blur(24px) saturate(180%);
        -webkit-backdrop-filter: blur(24px) saturate(180%);
        border: 1px solid rgba(16, 185, 129, 0.25);
        box-shadow: 0 8px 32px rgba(16, 185, 129, 0.15), inset 0 0 40px rgba(16, 185, 129, 0.03);
    }
.light .glass {
        background: linear-gradient(135deg, rgba(255, 255, 255, 0.85) 0%, rgba(240, 253, 250, 0.75) 100%);
        backdrop-filter: blur(24px) saturate(180%);
        -webkit-backdrop-filter: blur(24px) saturate(180%);
        border: 1px solid rgba(5, 150, 105, 0.2);
        box-shadow: 0 8px 32px rgba(5, 150, 105, 0.1), inset 0 0 40px rgba(5, 150, 105, 0.02);
    }
.glass-card {
        background: linear-gradient(135deg, rgba(16, 185, 129, 0.12) 0%, rgba(5, 150, 105, 0.06) 100%);
        backdrop-filter: blur(32px) saturate(180%);
        -webkit-backdrop-filter: blur(32px) saturate(180%);
        border: 1px solid rgba(16, 185, 129, 0.25);
        border-radius: 1.5rem;
        box-shadow: 0 10px 40px rgba(0, 0, 0, 0.3), 0 0 1px rgba(16, 185, 129, 0.2);
        transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);
        position: relative;
        overflow: hidden;
    }
.light .glass-card {
        background: linear-gradient(135deg, rgba(255, 255, 255, 0.9) 0%, rgba(240, 253, 250, 0.8) 100%);
        backdrop-filter: blur(32px) saturate(180%);
        -webkit-backdrop-filter: blur(32px) saturate(180%);
        border: 1px solid rgba(5, 150, 105, 0.15);
        box-shadow: 0 10px 40px rgba(0, 0, 0, 0.08), 0 0 1px rgba(5, 150, 105, 0.1);
    }
.glass-card::before {
        content: '';
        position: absolute;
        top: 0;
        left: -100%;
        width: 100%;
        height: 100%;
        background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.15), transparent);
        transition: left 0.7s ease;
    }
.glass-card:hover::before {
        left: 100%;
    }
.glass-card:hover {
        border-color: rgba(16, 185, 129, 0.5);
        box-shadow: 0 20px 60px rgba(16, 185, 129, 0.3), 0 0 100px rgba(16, 185, 129, 0.2), inset 0 0 40px rgba(16, 185, 129, 0.08);
        transform: translateY(-12px) scale(1.03);
    }
